<template>
<div class="backTop">
	<button class="btn btn-default" @click="backtop">
		<i class="iconfont icon-up"></i>
	</button>
</div>
</template>
<script>
export default {
	name: 'backTop',
	data () {
		return {
			msg: ''
		}
	},
	methods: {
		backtop(){
			let h = window.scrollY
			let s = setInterval(()=>{
				if(h>-1){
					window.scrollTo(0,h)
					h-=10
				}else{
					clearInterval(s)
				}
			}, 1)
		}
	}
}
</script>
<style scoped lang="less">
.backTop{position: fixed;right:20px;bottom:10%;z-index: 100;width:50px;height: 50px;border:1px solid #999;border-radius: 100%;
	button{width:100%;height: 100%; padding:0;outline: none;box-shadow: none;display: flex;justify-content:center;align-items: center;line-height: normal;
		i{font-size: 20px;color:#000;margin-top:5px;}
	}
}
</style>


