import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
//import $ from 'jQuery';

import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
Vue.use(Mint);

import VueAwesomeSwiper from 'vue-awesome-swiper';
Vue.use(VueAwesomeSwiper);

import '../node_modules/swiper/dist/css/swiper.css';

import '../node_modules/animate.css/animate.css';

import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';

import axios from 'axios';
Vue.prototype.$axios = axios;
axios.defaults.baseURL = 'https://d.apicloud.com';
let now = Date.now();
import SHA1 from './assets/SHA1.js';
axios.defaults.headers = {
	"Content-Type": "application/json;charset=UTF-8",
	"X-APICloud-AppId": "A6011233851659",
	"X-APICloud-AppKey": SHA1("A6011233851659"+"UZ"+"94501DE2-3E09-1CDF-609F-2663F447DA9A"+"UZ"+now)+"."+now
};

Vue.prototype.BUS = new Vue();

Vue.config.productionTip = false;

new Vue({
  	el: '#app',
  	data: {
  		lachewang: {}
  	},
  	mounted() {
  		this.init()
  	},
  	router,
  	store,
  	render: h => h(App),
  	methods: {
  		//初始化
  		init() {
			if(localStorage.lachewang) {
				this.lachewang = JSON.parse(localStorage.lachewang)
				if(!this.lachewang.system) {
					this.getSystem()
				} else {
					if(!this.lachewang.system.display) {
			            this.$router.push('/close')
			        }
				}
				if(!this.lachewang.catalogs) {
					this.getCatalogs()
				}
				if(!this.lachewang.friendlyLink) {
					this.getFlink()
				}
				if(!this.lachewang.works) {
					this.getWorks()
				}
			} else {
				this.lachewang.addCatalogDialog = false
				this.lachewang.isLogin = false
				this.getSystem()
				this.getCatalogs()
				this.getFlink()
				this.getWorks()
			}
	  	},
	  	//获取系统配置信息
	  	getSystem() {
	  		let that = this
	  		this.$axios({
				method: 'GET',
				url: '/mcm/api/system'
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					this.lachewang.system = res.data[0]
					localStorage.lachewang = JSON.stringify(that.lachewang)
				}
			}).catch(err => {
				console.log(err)
			})
	  	},
	  	//获取栏目
	  	getCatalogs() {
	  		let that = this
	  		this.$axios({
				method: 'GET',
				url: '/mcm/api/catalogs'
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.lachewang.catalogs = res.data
					localStorage.lachewang = JSON.stringify(that.lachewang)
				}
			}).catch(err => {
				console.log(err)
			})
	  	},
	  	//获取友链
	  	getFlink() {
	  		let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/flink?filter=' + encodeURIComponent('{"where": {"display": true}}')
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.lachewang.friendlyLink = res.data
					localStorage.lachewang = JSON.stringify(that.lachewang)
				} else { 
					this.flink = [] 
				}
			}).catch(err => {
				console.log(err)
			})
		},
		//获得作品列表
		getWorks() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/works?filter=' + encodeURIComponent('{"where": {"display": true}}')
			}).then(res => {
				if(res && res.data && res.data.length>0) {
					that.lachewang.works = res.data
					localStorage.lachewang = JSON.stringify(that.lachewang)
				} else { 
					that.lachewang.works = [] 
					localStorage.lachewang = JSON.stringify(that.lachewang)
				}
			}).catch(err => {
				console.log(err)
			})
		}
  	}
});


