<template>
  <div class="front" :class="this.showMenu">

    <mt-popup v-model="popupVisible" position="top" :modal="false" :closeOnClickModal="false">
      系统升级维护, 当前为静态资源浏览模式
      <button @click="closePopup" class="btn btn-default"><i class="iconfont icon-quxiaodingdan"></i></button>
    </mt-popup>

    <front-top v-if="lachewang.catalogs && lachewang.catalogs.length>0" :menu="lachewang.catalogs"></front-top>

    <div class="container" :style="mainStyle">
        <div class="row">
          <keep-alive>
            <router-view/>
          </keep-alive>
        </div>
    </div>

    <div class="foot">
      <div class="container">
        <friend-link :flink="lachewang.friendlyLink"></friend-link>
        <copy-right :systemConfig="lachewang.system" :isLogin="lachewang.isLogin"></copy-right>
      </div>
    </div>

    <transition mode="out-in" appear
      appear-active-class="animated fadeInUp"
      enter-active-class="animated fadeInUp"
      leave-active-class="animated fadeOutDown">
      <back-top v-if="showBackTop"></back-top>
    </transition>
  </div>
</template>
<script>
import Vue from 'vue'
import { Popup, Indicator } from 'mint-ui'
Vue.component(Popup.name, Popup);

import FrontTop from './common/Top'
import BackTop from './common/BackTop'
import FriendLink from './common/FriendLink'
import CopyRight from './common/CopyRight'

import { mapState } from 'vuex'
export default {
  name: 'Front',
  data() {
    return {
      showBackTop: false,
      popupVisible: false,
      lachewang:{},
      mainStyle:{}
    }
  },
  components: { FrontTop, FriendLink, CopyRight, BackTop },
  mounted () {
    Indicator.open({text: '加载中...',spinnerType: 'fading-circle'});
    let that = this
    setTimeout(() => {
        if(localStorage.lachewang) {
          that.init()
          Indicator.close();
        }
    }, 500)
    
    document.title = "首页";
    //setTimeout(()=>{this.popupVisible = true},2000)
    let dh = window.document.body.offsetHeight
    this.mainStyle = { minHeight: dh-180+'px' }

    //滚动条下拉时显示返回顶部
    window.onscroll = function() {
      if(window.scrollY>100) {
        if(!that.showBackTop){
          that.showBackTop=true
        }
      }else{
        that.showBackTop=false
      }
    }
  },
  methods: {
    //初始化
    init() {
      if(localStorage.lachewang) {
        this.lachewang = JSON.parse(localStorage.lachewang)
        if(this.lachewang.system) {
          if(!this.lachewang.system.display){
            this.$router.push('#/close')
          }
        }
      }
    },
    //关闭顶部弹窗
    closePopup() {
      this.popupVisible = false
    }
  },
  computed: {
     ...mapState(['showMenu'])
  }
}
</script>
<style scoped lang="less">
.front{-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;width:100%;height: 100%;transition: all 0.3s;
  .container{width:100%;}
  .row{height: 100%;}
  &.showMenu{margin-left:-50%;}
  .mint-popup{width:100%;height:60px;line-height:60px;background-color: rgba(0,0,0,0.5);color:#fff;text-align: center;display: flex;align-items: center;justify-content: center;
    button{position: absolute;right:20px;color:#fff;}
  }
  .foot{width:100%;padding: 30px 0;color:#777;box-sizing: border-box;}
}
</style>


